















var shop, userLoginInfo;
export default {
	data() {
		return {
			address: [],
			province: '',
			city: '',
			area: '',
			towns: '',
			detail: '',
			shopName: ''
		}
	},
	onLoad() {
		shop = uni.getStorageSync('locationShop');
		userLoginInfo = this.userLoginInfo('../mine/mine', 2);
		
		this.istrue = false;
		this.province = shop.province;
		this.city = shop.city;
		this.area = shop.area;
		this.towns = shop.towns;
		this.detail = shop.detail;
		this.shopName = shop.shop_name;
	},
	methods: {
		saveSaleOrder() {
			if (userLoginInfo.identity == 1) {
				return;
			}
			uni.showModal({
				title: '提示',
				content: '是否领取赠品礼物，领取后点击确定',
				success: res => {
					if (res.confirm) {
						uni.showLoading({
							title: '加载中..'
						})
						uni.request({
							url: this.apiUrl + 'user/saveSaleOrder',
							method: 'GET',
							data: {
								uid: userLoginInfo.id
							},
							success: orderRes => {
								if (orderRes.data.code == 0 && orderRes.data.data.status == 3) {
									uni.showToast({
										title: '领取成功',
										icon: 'none',
										success: (res) => {
											setTimeout(() => {
												uni.navigateBack({
													delta: 1
												});
											}, 500);
										},
									})
								}
							},
							fail: () => {},
							complete: () => {
								uni.hideLoading();
							}
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});

		},
	}
}
